.background{
    position: absolute;
    top:0;
    left:0;
    background-image: url('./backgroundImg.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    width: 100vw;
    opacity: 0.3;
    z-index: -1;
}

.name_tiles{
    display: flex;
    flex-direction: row;
    border: 1px solid blue;
    border-right: none;
    border-left: none;
    border-radius: 3px;
    padding: 10px 0;
}
.name_tile{
    margin: auto;
    color: darkblue;
}

.footer{
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 6vh;
    min-height: 65px;
    color: white;
    background-color: rgb(50,50,50);
    padding: 10px 0 0;
    display: flex;
    flex-direction: column;
}

.footer:first-child{
    margin: auto;
}

.notConnected{
    color: white;
    font-size: 120px;
}

.loading{
    position: fixed;
    top:0;
    left:0;
    background-color: rgba(0,0,0,.8);
    height: 100vh;
    width: 100vw;
    color: white;
    text-align: center;
    font-size: 20px;
    z-index: 1000000;
}

.loading h2{
    margin-top: 20vh;
    margin-bottom: 10px;
}
.loadingAnimation{
  border: 6px solid rgb(8,8,8); /* Light grey */
  border-top: 6px solid #FFF; /* white */
  border-radius: 50%;
  width: 50px;
  height: 50px;
  animation: spinLoader 2s linear infinite;
  margin: 25px auto;
}

@keyframes spinLoader {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }