.container {
    width: 90%;
    border: 1px solid white;
    margin: auto auto 10vh;
    color: #DCDCDC;
    border-radius: 10px;
}

.hintText{
    margin: 10px auto;
}

.search{
    position: relative;
    background-color: rgb(5,5,5);
    padding: 3px 25px;
    border: 1px solid #555;
    border-radius: 5px;
    width: 40%;
    margin: 10px auto;
    display: flex;
    flex-direction: row;    
}

.search input{
    padding: 8px 6px;
    width: 95%;
    background-color: rgb(5,5,5);
    border: none;
    color: white;
    outline: none;
}

.searchIcon{
    position: absolute;
    top: 0;
    right: 5px;
    height: 100%;
    display: flex;
    justify-content: center;
    color:#999;
    cursor: pointer;
}
.searchIcon svg {
    margin: auto;
    
}