.container {
    position: relative;
    height: 100%;
}
.title{
    position: relative;
    text-transform: capitalize;
    font-size: 20px;
    font-weight: 600;
    text-align: left;
    padding-left: 5px;    

}
.length{
    position: absolute;
    top:5px;
    right:5px;
    font-size: 12px;
    font-weight: 400;


}

.midSection{
    display: flex;
    flex-direction: row;
    background-color: rgb(15,15,15);
    padding: 5px;
    margin: 10px 5px;
    border-radius: 5px;

}
.bigImg,
.img{
    /* background-image: url("./toystory.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;     */
    width: 35%;
    margin: 5px;
    /* box-shadow: 1px 1px 2px #999; */
    border-radius: 5px;
}

.bigImg img,
.img img{
    height: 100%;
    widtH: 100%;

}
.bigImg{
    height: 250px;
}
.img{
    height: 150px;
}

.bigDescription,
.description{
    margin: 5px;
    line-height: 1.8;
    width: 50%;  
    max-height: 600px;  
    overflow-y: scroll;    
    color: #BDBDBD;
}
.description{
    font-size: 14px;
}

.bigDescription{    
    font-size: 18px;    
}

.rating{
    position: absolute;
    bottom: 0;
    right: 5px;
    display: flex;
    justify-content: center;
    /* border: 1px solid black; */
    color:  #ff9800
    ;

}

.rating div{
    /* border: 1px solid red; */
