.container {
    position: relative;
    height: 100%;
}
.title{
    position: relative;
    text-transform: capitalize;
    font-size: 20px;
    font-weight: 600;
    text-align: left;
    padding-left: 5px;    

}
.length{
    position: absolute;
    top:5px;
    right:5px;
    font-size: 12px;
    font-weight: 400;


}

.midSection{
    display: flex;
    flex-direction: row;
    background-color: rgb(15,15,15);
    padding: 5px;
    margin: 10px 5px;
    border-radius: 5px;

}
.img{
    width: 50%;            
    margin: 5px auto;
}

.img img{
    height: 100%;
    widtH: 100%;

}

.rating{
    position: absolute;
    bottom: 0;
    right: 5px;
    display: flex;
    justify-content: center;
    /* border: 1px solid black; */
    color:  #ff9800
    ;

}

.rating div{
    /* border: 1px solid red; */
    padding: 0;
    margin: auto;
    
}
.actors,
.ganres{
    display: flex;
    flex-direction: row;
    font-size: 12px;
    margin: 5px;
}
.actors span,
.ganres span{
    font-size: 16px;
    font-weight: 600;
   
}