.container{
    background-color: rgba(15,15,15, .75);
    margin: 5vh auto;    
    width: 90%;
    max-width: 650px;
    box-shadow: 1px 1px 2px #000;
    border-radius: 10px;
    padding-bottom: 10px;
    
}
.header {
    padding: 20px 0px;
    margin-bottom: 30px;
    font-size: 20px;
    background-color: #ffc107;
    width: 100%;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    font-weight: 600;
    display: flex;
    flex-direction: row;
    justify-content: center;

}

.question {
    color: white;
    font-size: 18px;    
    line-height: 2;
    padding: 0 10px;
    margin: 20px auto;
}

.container form{
    display: flex;
    justify-content: center;
}

.btnWrapper{
    margin: 5px;
}
.answerInput img{
    width: 60%;
    margin-bottom: 5px;
}
.answerBtn{
    margin: 15px auto;
}


.a label{
    color: white !important;
}

.loading{
    position: fixed;
    top:0;
    left:0;
    width: 100vw;
    height: 100vh;
    z-index: 999;
    background-color: rgba(0,0,0,.5);
    display: none;
    
}
.show{
    display: inline !important;
}

.warning{
    color: white;

    font-size: 13px;

}