.container{
    background-color: rgb(5,5,5,.5);
    margin: 5vh auto;    
    width: 90%;
    max-width: 650px;
    box-shadow: 1px 1px 2px #999;
    border-radius: 10px;
    padding-bottom: 10px;
    color: white;
}
.badHeader,
.goodHeader {
    padding: 20px 0px;
    margin-bottom: 15px;
    font-size: 20px;    
    width: 100%;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    font-weight: 600;
    display: flex;
    flex-direction: row;
    justify-content: center;

}

.goodHeader{
    background-color: lightgreen;
    color: green;
}

.badHeader{
    background-color: salmon;
    color: red;
}

.question {
    font-size: 16px;    
    line-height: 2;
    padding: 0 10px;
    margin: 20px auto;
}

.movieArea{
    width: 80%;
    /* height: 30vh;     */
    margin: 10px auto;
    padding: 10px;
    background-color: black;
    border-radius: 10px;
}

.answerBtn{
    margin: 15px auto;
}